@import './variables.css';

.home-container {
  width: 1200px;
  margin: 0 auto;
  height: 800px;
  background-color: #000000;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 40px;
  padding: 125px 0 0 0;
  color: #FFFFFF;
  overflow: hidden;
  position: relative;
}

/* Info panel styling */
.info-panel {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 18px;
  padding-right: 14px;
  min-width: 0;
  position: relative;
  animation: slideInFromLeft 0.9s ease-out forwards;
}

.profile-avatar {
  width: 300px;
  height: 300px;
  border-radius: 35px;
  box-shadow: 0 0 25px var(--my-color);
  object-fit: cover;
  border: 2px solid var(--my-color);
}

.neon-text {
  font-size: 4.5rem;
  font-weight: bold;
  margin: 0 0 10px 0;
  color: var(--my-color);
  text-shadow: 0 0 18px var(--my-color);
}

.wave-icon-home {
  display: inline-block;
  font-size: 1.5em;
  animation: waveAnimation 2s infinite;
  transform-origin: 70% 70%;
}

.neon-text-2 {
  font-size: 1.9rem;
  font-weight: 600;
  margin: 0 0 20px 0;
  color: #bdeaff;
  text-shadow: 0 0 8px var(--my-color);
}

.description {
  font-size: 1.3rem;
  margin-top: 5px;
  color: #eaf6ff;
}

.social-icons {
  margin-top: 10px;
  padding: 10px 10px 10px 0;
}

.social-icons a {
  color: var(--my-color);
  font-size: 28px;
  margin-right: 15px;
  transition: transform 0.3s ease, color 0.2s;
  display: inline-block;
}

.social-icons a:hover {
  transform: scale(1.3);
  color: #d4eb05;
}

.button-home {
  display: flex;
  gap: 15px;
  padding: 30px 0;
}

.button-container-home {
  display: inline-block;
  position: relative;
  overflow: hidden;
  background: #fff;
  color: #000;
  padding: 1rem 2rem;
  font-weight: 900;
  text-transform: uppercase;
  cursor: pointer;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 100%;
}

.button-container-home::before {
  content: '';
  position: absolute;
  inset: 0;
  background: #000;
  clip-path: polygon(100% 0, 100% 100%, 0% 100%, 100% 100%);
  transition: clip-path .3s ease;
  z-index: 1;
}

/* Layer that moves up on hover */
.button-container-home:hover:before {
  clip-path: polygon(100% 0, 0 0, 0% 100%, 100% 100%);
}

.text-for-button-home {
  position: relative;
  display: block;
  z-index: 2;
  color: #000;
  /* Màu chữ mặc định là đen */
  transition: color .3s;
  /* Không dùng mix-blend-mode nếu không muốn bị đảo màu khó kiểm soát */
}

.button-container-home:hover .text-for-button-home {
  color: #fff;
  /* Khi hover, chữ thành trắng trên nền đen */
  animation: move-up .3s ease forwards;
}

@keyframes move-up {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(80%);
  }

  51% {
    transform: translateY(-80%);
  }

  100% {
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 1100px) {
  .home-container {
    width: 100vw;
    gap: 10px;
    padding: 0 8px;
  }

  .chat-area {
    width: 340px;
    padding: 13px 7px 10px 7px;
  }

  .info-panel {
    padding-left: 6px;
    padding-right: 2px;
  }
}

@media (max-width: 900px) {
  .home-container {
    flex-direction: column;
    align-items: center;
    height: auto;
    min-height: 100vh;
    gap: 22px;
    padding: 0;
  }

  .info-panel {
    width: 99vw;
    max-width: 700px;
    align-items: center;
    padding: 0;
    text-align: center;
  }

  .profile-avatar {
    margin: 0 auto 10px auto;
  }
}

@media (max-width: 600px) {
  .home-container {
    padding: 120px 0 0 0;
    gap: 8px;
  }

  .info-panel {
    max-width: 99vw;
    padding: 0;
  }

  .profile-avatar {
    width: 250px;
    height: 250px;
    border-radius: 20px;
  }

  .neon-text {
    font-size: 2.5rem;
    width: 100%;
    text-align: left;
    padding-left: 65px;
    padding-top: 20px;
  }

  .neon-text-2 {
    font-size: 1.02rem;
    margin-bottom: 10px;
  }

  .description {
    font-size: 0.97rem;
    margin-top: 6px;
    text-align: justify;
    padding: 0 30px;
  }

  .social-icons {
    display: flex;
    justify-content: center;
    padding: 10px 0;
    margin: 0;
  }

  .social-icons a {
    font-size: 30px;
    margin: 0 10px;
  }

  .button-home {
    display: flex;
    flex-wrap: wrap;
    /* Cho phép xuống hàng khi không đủ chỗ */
    gap: 10px;
    justify-content: center;
    /* Căn trái, bạn có thể dùng center nếu muốn */
  }

  .button-container-home {
    flex: 1 1 calc(33.333% - 20px);
    min-width: 100px;
    box-sizing: border-box;
    padding: 20px;
    font-size: 0.9rem;
    text-align: center;
  }

  .text-for-button-home {
    padding: 0 auto;
    margin: 0 auto;
  }
}